html {
  position: relative;
  min-height: 100%;
}
body {
  /* Margin bottom by footer height */
  margin-bottom: 60px;
}
.equal,
.equal > div[class*='col-'] {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex: 1 1 auto;
}
.mbn {
  margin-bottom: 0 !important;
}
.mtn {
  margin-top: 0 !important;
}
.mts {
  margin-top: 10px !important;
}
.mtm {
  margin-top: 15px !important;
}
.mls {
  margin-left: 10px !important;
}
.mbs {
  margin-bottom: 10px !important;
}
.mts {
  margin-top: 10px !important;
}
.mrs {
  margin-right: 10px !important;
}
.mrm {
  margin-right: 15px !important;
}
.mlm {
  margin-left: 15px !important;
}
.pln {
  padding-left: 0 !important;
}
.prn {
  padding-right: 0 !important;
}
.ptn {
  padding-top: 0 !important;
}
.pbn {
  padding-bottom: 0 !important;
}
.w100 {
  width: 100% !important;
}
.overflowFix {
  overflow: auto !important;
}
.vertical-scroll {
  overflow-y: auto;
}
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: auto;
  background-color: #f5f5f5;
  z-index: 10001;
}
body.map footer.footer {
  position: relative;
}
.fitTable {
  word-wrap: break-word;
  table-layout: fixed;
}
.truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}
.truncate.truncate-50 {
  width: 50px;
}
.truncate.truncate-80 {
  width: 80px;
}
.truncate.truncate-150 {
  width: 150px;
}
.truncate.truncate-250 {
  width: 250px;
}
.modal.equipment .equipmentObservationHeaders {
  display: none;
}
.modal.equipment.MOVEABLE .equipmentObservationHeaders.MOVEABLE,
.modal.equipment.WELL .equipmentObservationHeaders.WELL,
.modal.equipment.SOIL_MOISTURE .equipmentObservationHeaders.SOIL_MOISTURE,
.modal.equipment.null .equipmentObservationHeaders.null {
  display: block;
}
.modal .alert.confirmDelete {
  position: absolute;
  top: 20%;
  width: 50%;
  left: 25%;
}
.editable-container.editable-inline {
  position: relative;
  z-index: 1;
}
table .editable-container.editable-inline {
  background-color: #fff;
}
.clear {
  clear: both !important;
}
.panel.panel-primary .text-muted {
  color: #ffffff;
}
.btn span.fa {
  position: absolute;
  top: 0;
  left: 0;
  width: 1.8em;
  height: 100%;
  line-height: 2;
}
/*Tabbed content */
.tab-content {
  background-color: #ffffff;
  padding: 15px;
}
/* Show on hover */
.reveal-on-hover-wrap .reveal-on-hover-element {
  visibility: hidden;
}
.reveal-on-hover-wrap:hover .reveal-on-hover-element {
  visibility: visible;
}
/* Editable click */
.editable-click {
  border-bottom: dashed 1px rgba(255, 255, 255, 0) !important;
}
.editable-click:hover {
  text-decoration: none;
  border-bottom: dashed 1px #0088cc !important;
  cursor: context-menu;
}
.editable-click:hover:after {
  font-size: 10px;
  color: #08c;
  content: "\f044";
  font: normal normal normal 14px/1 FontAwesome;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(0px, 0);
  margin: 0px 0px 0 5px;
  display: inline-block;
  top: -5px;
  position: relative;
}
/* Footer styling */
footer .links li {
  list-style: none;
  margin-right: 10px;
  padding-right: 10px;
  border-right: 1px solid #ebebeb;
}
footer .links li:last-of-type {
  margin-right: 0px;
  padding-right: 0px;
  border-right: none;
}
.pointer {
  cursor: pointer;
}
/* List group items */
.list-group .list-group-item {
  padding: 0;
}
.list-group .list-group-item.active a {
  color: #ffffff;
}
.list-group .list-group-item a {
  padding: 10px 15px;
  display: block;
}
.list-group .list-group-item span.badge {
  margin-top: 10px;
  margin-right: 15px;
}
body.loading {
  cursor: wait;
}
.blur {
  transition: 1s filter linear;
  filter: blur(5px) grayscale(20%);
  pointer-events: none;
}
@media (max-width: 1440px) {
  table tr .btn-group button.btn-sm {
    min-height: 30px;
    padding: 0 .5em;
    text-align: center;
  }
  table tr .btn-group button.btn-sm span.glyphicon,
  table tr .btn-group button.btn-sm span.fa {
    background: inherit;
    box-shadow: inherit;
    position: relative;
    min-height: 28px;
  }
  table tr .btn-group button.btn-sm .hidden-xs {
    display: none!important;
  }
}
